.custom-checkbox {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  user-select: none;
}
.custom-checkbox .checkbox-box {
  width: 20px;
  height: 20px;
  border: 2px solid #334155;
  border-radius: 4px;
  background: #1E293B;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  flex-shrink: 0;
}
.custom-checkbox .checkbox-box .check-icon {
  color: #ffffff;
}
.custom-checkbox .checkbox-box.checked {
  background: #10B981;
  border-color: #10B981;
}
.custom-checkbox .checkbox-label {
  color: #ffffff;
  font-size: 12px;
  line-height: 1.4;
}
.custom-checkbox:hover:not(.disabled) .checkbox-box {
  border-color: #10B981;
}
.custom-checkbox:hover:not(.disabled) .checkbox-box:not(.checked) {
  background: rgba(16, 185, 129, 0.05);
}
.custom-checkbox.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

:host-context(body.light-theme) .custom-checkbox .checkbox-box {
  background: #ffffff;
  border-color: #cbd5e1;
}
:host-context(body.light-theme) .custom-checkbox .checkbox-box .check-icon {
  color: #ffffff;
}
:host-context(body.light-theme) .custom-checkbox .checkbox-box.checked {
  background: #10B981;
  border-color: #10B981;
}
:host-context(body.light-theme) .custom-checkbox .checkbox-label {
  color: #0f172a;
}
:host-context(body.light-theme) .custom-checkbox:hover:not(.disabled) .checkbox-box {
  border-color: #10B981;
}
:host-context(body.light-theme) .custom-checkbox:hover:not(.disabled) .checkbox-box:not(.checked) {
  background: rgba(16, 185, 129, 0.05);
}
